import * as React from 'react';
import logo from '../../../assets/easybid.logo.png'
import {useBid} from "../../../hook/useBid";
import {ChangeEventHandler, useState} from "react";
import Storage from "../../../Utils/Storage";
import API from "../../../model/API";
import {useNavigate} from "react-router-dom";

export const MainHeader: React.FC = () => {

    const {getBidItems, onSaleIndex, bidItems} = useBid()
    const fileName = Storage.getFileName() ?? ""

    const [isEdit, setIsEdit] = useState<boolean>(fileName === "")
    const [bidName, setBidName] = useState<string>(fileName)

    const onChange: ChangeEventHandler<HTMLInputElement> = (event) =>{
        setBidName(event.target.value)
    }

    const edit = async () =>{
        Storage.saveFileName(bidName)
        setIsEdit(false)
        const id = Storage.getEbId()
        API.shared.init(id, bidName).then(getBidItems)
    }

    return (
        <>
        <div><img style={{cursor:"pointer"}} onClick={ ()=>{window.location.reload()}} className='mt-5' src={logo} alt="reload"/></div>
        <div className="row m-1">
            <input
                id="input_name"
                className="input-group-sm input-group-tex col-9"
                placeholder="경매 이름 입력하기"
                onChange={onChange}
                disabled={!isEdit}
                value={bidName}
            />
            {
                isEdit ?
                    <button
                        onClick={edit}
                        id="button_reload"
                        className="col btn btn-info mx-2"
                    >저장</button> :
                    <button
                        onClick={()=>{setIsEdit(true)}}
                        id="button_reload"
                        className="col btn btn-info mx-2"
                    >변경</button>
            }
        </div>

        {
            fileName !== "" &&
            <>
                <a
                    className="btn btn-secondary w-100 m-1"
                    href="/seller"
                    target="_blank"
                >구매자 리스트 링크 열기</a>
                <a
                    className="btn btn-secondary w-100 m-1"
                    href="/product"
                    target="_blank"
                >판매 상품 링크 열기</a>

                <div className="card w-100 m-1">
                    <div className="card-header">
                        판매중인 상품
                    </div>
                    {
                        onSaleIndex === -1 ?
                            <div className="d-flex card-body flex-column">
                                <p>판매 중인 상품이 없습니다.</p>
                            </div> :
                            <div id="div-sale-on" className="d-flex card-body flex-column">
                                <p>상품명 : {bidItems[onSaleIndex].name}</p>
                                <p>구매 갯수 : { bidItems[onSaleIndex].saleAmount}</p>
                                {
                                    bidItems[onSaleIndex].amount !== 0 &&
                                    <p>남은 갯수 : {bidItems[onSaleIndex].amount - bidItems[onSaleIndex].saleAmount}</p>
                                }
                            </div>
                    }



                </div>
            </>
        }

        <hr/>
    </>)
}